export default {
	data() {
		return {
			num: 0,
			popups: false,
			id:'',
			content:'',
			allNums:'',
		}
	},
	onLoad: function (option) { //option为object类型，会序列化上个页面传递的参数
		let _this = this;
		_this.id = option.id;
	},
	mounted:function(){
		var _this = this;
		_this.userintegralDetail();
	},
	methods: {
		reduce() {
			var _this = this;
			if(_this.num>=1){
				_this.num--
			}
		},
		add() {
			var _this = this;
			if(_this.num<_this.allNums){
				_this.num++
			}
		},
		popupIndex() {
			var _this = this;
			if (_this.popups) {
				_this.popups = false
			} else{
				_this.popups = true
			}
		},
		popupYes() {
			var _this = this;
			_this.popups = false
			uni.navigateTo({
				url:"integral_handle",
			})
		},
		//详情
		userintegralDetail:function(){
			let _this=this;
			let data={
				integral_id:_this.id,
			};
			_this.$axios(_this.$baseUrl.userintegralDetail,data).then(res =>{
				if(res.data.status==1){
					_this.content = res.data.result.integral;
					_this.allNums = res.data.result.integral.store_count;
				}
			}).catch(error =>{
				
			})
		},
	}
}
